@import url('https://fonts.googleapis.com/css2?family=Inter:wght@300;400;500;600;700;800;900&display=swap');
@tailwind base;
@tailwind components;
@tailwind utilities;

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html {
  scroll-behavior: smooth;
}

body {
  font-family: 'Inter', system-ui, -apple-system, sans-serif;
  background: linear-gradient(135deg, #0f172a 0%, #1e293b 50%, #334155 100%);
  min-height: 100vh;
  color: #f8fafc;
  overflow-x: hidden;
}

/* Custom scrollbar */
::-webkit-scrollbar {
  width: 8px;
}

::-webkit-scrollbar-track {
  background: rgba(15, 23, 42, 0.5);
}

::-webkit-scrollbar-thumb {
  background: linear-gradient(135deg, #3b82f6, #8b5cf6);
  border-radius: 4px;
}

::-webkit-scrollbar-thumb:hover {
  background: linear-gradient(135deg, #10b981, #06b6d4);
}

/* Glass morphism utilities */
.glass {
  background: rgba(15, 23, 42, 0.8);
  backdrop-filter: blur(20px);
  border: 1px solid rgba(59, 130, 246, 0.2);
}

.glass-strong {
  background: rgba(15, 23, 42, 0.9);
  backdrop-filter: blur(30px);
  border: 1px solid rgba(59, 130, 246, 0.3);
}

/* Remove all gradient text styling - make headings solid colors */
.gradient-text {
  color: #3b82f6 !important;
  background: none !important;
  -webkit-background-clip: unset !important;
  -webkit-text-fill-color: unset !important;
  background-clip: unset !important;
}

.gradient-text-alt {
  color: #8b5cf6 !important;
  background: none !important;
  -webkit-background-clip: unset !important;
  -webkit-text-fill-color: unset !important;
  background-clip: unset !important;
}

/* Hover effects */
.hover-glow {
  transition: all 0.3s ease;
}

.hover-glow:hover {
  box-shadow: 0 0 30px rgba(59, 130, 246, 0.3);
  transform: translateY(-2px);
}

/* Button styles */
.btn-primary {
  background: linear-gradient(135deg, #3b82f6, #8b5cf6);
  transition: all 0.3s ease;
}

.btn-primary:hover {
  background: linear-gradient(135deg, #10b981, #06b6d4);
  transform: translateY(-2px);
  box-shadow: 0 10px 30px rgba(59, 130, 246, 0.3);
}

.btn-secondary {
  background: rgba(59, 130, 246, 0.1);
  backdrop-filter: blur(20px);
  border: 1px solid rgba(59, 130, 246, 0.3);
  transition: all 0.3s ease;
  color: #f8fafc;
}

.btn-secondary:hover {
  background: rgba(59, 130, 246, 0.2);
  transform: translateY(-2px);
}

/* Form styles */
.form-input {
  background: rgba(15, 23, 42, 0.8);
  backdrop-filter: blur(20px);
  border: 1px solid rgba(59, 130, 246, 0.3);
  transition: all 0.3s ease;
  color: #f8fafc;
}

.form-input:focus {
  background: rgba(15, 23, 42, 0.9);
  border-color: #3b82f6;
  box-shadow: 0 0 20px rgba(59, 130, 246, 0.3);
}

.form-input::placeholder {
  color: #94a3b8;
}

/* Card animations */
.card-hover {
  transition: all 0.3s ease;
}

.card-hover:hover {
  transform: translateY(-5px) scale(1.02);
  box-shadow: 0 20px 40px rgba(59, 130, 246, 0.2);
}

/* Loading spinner */
.spinner {
  border: 3px solid rgba(59, 130, 246, 0.2);
  border-top: 3px solid #3b82f6;
  border-radius: 50%;
  animation: spin 1s linear infinite;
}

@keyframes spin {
  0% { transform: rotate(0deg); }
  100% { transform: rotate(360deg); }
}

/* Progress bar */
.progress-bar {
  background: linear-gradient(90deg, #3b82f6, #10b981, #8b5cf6);
  transition: width 0.5s ease;
}

/* Floating elements */
.floating {
  animation: float 6s ease-in-out infinite;
}

.floating:nth-child(2) {
  animation-delay: -2s;
}

.floating:nth-child(3) {
  animation-delay: -4s;
}

@keyframes float {
  0%, 100% { transform: translateY(0px); }
  50% { transform: translateY(-20px); }
}

/* Mobile optimizations */
@media (max-width: 768px) {
  .glass {
    backdrop-filter: blur(15px);
  }
  
  .glass-strong {
    backdrop-filter: blur(20px);
  }
}

/* Accessibility */
@media (prefers-reduced-motion: reduce) {
  * {
    animation-duration: 0.01ms !important;
    animation-iteration-count: 1 !important;
    transition-duration: 0.01ms !important;
  }
}

/* Focus styles */
.focus-ring:focus {
  outline: 2px solid #3b82f6;
  outline-offset: 2px;
}

/* Selection styles */
::selection {
  background: rgba(59, 130, 246, 0.3);
  color: #f8fafc;
}

/* Dark theme professional color overrides */
h1, h2, h3, h4, h5, h6 {
  color: #f8fafc !important;
}

/* Major headings - consistent white color */
.text-white {
  color: #f8fafc !important;
}

/* Subheadings and secondary text - light gray */
.text-gray-300, .text-gray-400 {
  color: #cbd5e1 !important;
}

/* Body text - medium gray */
.text-slate-600 {
  color: #94a3b8 !important;
}

/* Links and accents - blue */
.text-accent-blue, .text-blue-400, .text-blue-500 {
  color: #3b82f6 !important;
}

/* Success/green elements */
.text-accent-green, .text-green-400 {
  color: #10b981 !important;
}

/* Purple accents */
.text-accent-purple, .text-purple-400 {
  color: #8b5cf6 !important;
}

/* Sea green accents */
.text-accent-seagreen {
  color: #06b6d4 !important;
}

/* Warning/yellow elements */
.text-yellow-400 {
  color: #fbbf24 !important;
}

/* Error/red elements */
.text-red-400 {
  color: #f87171 !important;
}

/* Background color overrides for dark theme */
.bg-accent-blue\/20 {
  background-color: rgba(59, 130, 246, 0.2) !important;
}

.bg-accent-purple\/20 {
  background-color: rgba(139, 92, 246, 0.2) !important;
}

.bg-accent-green\/20, .bg-green-500\/20 {
  background-color: rgba(16, 185, 129, 0.2) !important;
}

.bg-yellow-500\/20 {
  background-color: rgba(251, 191, 36, 0.2) !important;
}

.bg-red-500\/20 {
  background-color: rgba(248, 113, 113, 0.2) !important;
}

.bg-blue-500\/20 {
  background-color: rgba(59, 130, 246, 0.2) !important;
}

/* Dark background updates */
.bg-gradient-to-br {
  background: linear-gradient(to bottom right, #0f172a, #1e293b, #334155) !important;
}

.bg-slate-50, .bg-blue-50, .bg-violet-50 {
  background: #0f172a !important;
}

/* Border colors for dark theme */
.border-white\/10 {
  border-color: rgba(59, 130, 246, 0.2) !important;
}

.border-white\/20 {
  border-color: rgba(59, 130, 246, 0.3) !important;
}

.border-blue-500\/10 {
  border-color: rgba(59, 130, 246, 0.1) !important;
}

/* Highlighted content styling */
.highlight-content {
  background: rgba(59, 130, 246, 0.1);
  border: 1px solid rgba(59, 130, 246, 0.3);
  color: #f8fafc;
}

/* Tools and filters highlighting */
.filter-highlight {
  background: rgba(16, 185, 129, 0.1);
  border: 1px solid rgba(16, 185, 129, 0.3);
  color: #f8fafc;
}

/* Interactive elements highlighting */
.interactive-highlight {
  background: rgba(139, 92, 246, 0.1);
  border: 1px solid rgba(139, 92, 246, 0.3);
  color: #f8fafc;
}

/* Key features highlighting */
.feature-highlight {
  background: rgba(6, 182, 212, 0.1);
  border: 1px solid rgba(6, 182, 212, 0.3);
  color: #f8fafc;
}